//@ts-nocheck
import { EventEmitter } from 'events'
import * as Sentry from '@sentry/react'

import {
	SocketClientOption,
	Message
} from './type'

const DEFAULT_OPTIONS = {
	reconnectTimeout: 2000,
	pingIntervalTimeout: 5000
}

export class SocketClient extends EventEmitter {
	private connectOptions: SocketClientOption
	private ws: WebSocket
	private pingInterval: any

	constructor(options: SocketClientOption) {
		super()
		this.connectOptions = {...options}
		this.connectOptions.reconnectTimeout = options.reconnectTimeout || DEFAULT_OPTIONS.reconnectTimeout
		this.connectOptions.pingIntervalTimeout = options.pingIntervalTimeout || DEFAULT_OPTIONS.pingIntervalTimeout

		this.connectWS()
	}

	private connectWS() {
		console.log('connecting to: ', this.connectOptions.wsURL)
		const { wsURL, token, connectionType, uid, version } = this.connectOptions
		if (!this.connectOptions.token) {
			return this.emit('connect-failed', { message: 'Missing token'})
		}
		this.ws = new WebSocket(`${wsURL}?token=${token}&type=${connectionType}&uid=${uid}&ver=${version}`)

		this.ws.addEventListener('open', this.wsOpenHandler)
		this.ws.addEventListener('message', this.wsMessageHandler)
		this.ws.addEventListener('close', this.wsCloseHandler)
		this.ws.addEventListener('error', this.wsErrorHandler)
	}

	private wsOpenHandler = (e: MessageEvent) => {
		this.emit('open', e)
		if (this.connectOptions.pingIntervalTimeout) {
      this.pingInterval = setInterval(() => {
				this.send({
					type: 'ping'
				})
			}, this.connectOptions.pingIntervalTimeout)
    }
	}

	private wsMessageHandler = (e: MessageEvent) => {
		const message: Message = JSON.parse(e.data)
		this.emit('message',{type: message.type, payload: message.payload})
		console.log('onmessage: ', message.type)
	}

	private wsCloseHandler = (e: MessageEvent) => {
		console.log('Socket is close: ', e)
		this.emit('close', e)
		if (this.pingInterval) {
			clearInterval(this.pingInterval)
		}
		// if (this.connectOptions.reconnectTimeout) {
    //   setTimeout(() => {
    //     this.connectWS()
    //   }, this.connectOptions.reconnectTimeout)
    // }
	}

	private wsErrorHandler = (e: MessageEvent) => {
		console.log('Socket connect error: ', JSON.stringify(e))
		Sentry.captureException(e)
		// this.emit('message', {type: 'error', payload: e})
	}

	send(message: any) {
		this.ws.send(JSON.stringify(message))
	}

}