export interface Message {
	type: string
	payload: any
}

export interface SocketClientOption {
	token: string
	wsURL: string
	connectionType: number
	uid: string
	version: number
	reconnectTimeout?: number
	pingIntervalTimeout?: number
}

export enum SocketMessageType {
	MAKE_CALL = 'makeCall',
	METADATA = 'metadata',
	READY = 'ready',
	SIGNAL = 'signal',
	USERS = 'users',
	HANG_UP = 'hangUp',
	CALLING = 'calling',
	RINGING = 'ringing',
	ACCEPT = 'accept',
	REJECT = 'reject',
	ENDCALL = 'end',
	PUBTRACK = 'pubTrack',
	SUBTRACK = 'subTrack',
	CONFIG = 'config',
	WRAPUP = 'wrapup',
	KICKOUT = 'kickout',
	QUEUE_INFO = 'queueInfo'
}

interface MessagePayload {
	fromUserId: string
	toUserId: string
	roomId: string
}
export interface MakeCallPayload {
	queueId: string
	requestType: string
	customerType: number
	metadata: string
	deviceInfo: string
}

export interface CallingPayload extends MessagePayload {}
export interface RingingPayload extends MessagePayload {}
export interface AnswerPayload extends MessagePayload {}
export interface RejectPayload extends MessagePayload {}
export interface EndPayload extends MessagePayload {}
export interface BusyPayload {
	fromUserId: string
}

export interface InQueuePayload {
	fromUserId: string
}

export interface ReadyPayload {
	roomId: string
	fromUserId: string
	nickname: string
	toUserId: string
}
export interface SDPSignalPayload {
	userId: string
	signal: {
		type: string
		sdp: string
	}
}
export interface CandidateSignalPayload {
	userId: string
	signal: {
		candidate: {
			candidate: string
			sdpMid: string
			sdpMLineIndex: number
			usernameFragment: string
		}
	}
}

export interface PubTrackPayload {
	trackId: {
		id: string
		streamId: string
		pubClientId: string
		peerId: string
		kind: number
		type: number
	}
}

export interface subTrackPayload {
	trackId: {
		id: string
		streamId: string
		type: number
		pubClientId: string
	}
}