import { ReactElement, useEffect, useState, useCallback, useRef } from 'react'
import * as Sentry from "@sentry/react"

import { MdCallEnd, MdMic, MdMicOff, MdVideocam, MdVideocamOff, MdCall } from 'react-icons/md'
import Peer from 'simple-peer'
import { 
	MakeCallPayload,
	SocketMessageType,
	ReadyPayload,
	AnswerPayload,
	SocketClient,
} from '../sdk/src'
import { initSocketClient } from '../services/socketClient'

import Camera from '../components/Camera'
import { TrackEventType } from '../types'

interface Props {
	localStream: MediaStream | null
}

interface ConnectionData {
	roomId: string
	userId: string
	fromUserId: string
}

let peerOptions = {
	initiator: false,
	// trickle: false,
	config: {
		iceServers: []
	},
	offerConstraints: {
		offerToReceiveAudio: true,
		offerToReceiveVideo: true,
	}
}
let peer: Peer.Instance | null

const initPeer = () => {
	console.log('init peer', peerOptions)
	peer = new Peer(peerOptions)
}

const connectionData: ConnectionData = {
	roomId: '',
	userId: '',
	fromUserId: '',
}

enum CONVERSATION_STATUS {
	START,
	WAITING,
	IN_CALL
}

let socketClient: SocketClient

export default function MakeCall({ localStream }: Props): ReactElement {
	const [remoteStream, setRemoteStream] = useState<MediaStream | null>(null)
	const [enableMic, setEnableMic] = useState(true)
	const [enableCam, setEnableCam] = useState(true)
	const [conversationStatus, setConversationStatus] = useState(CONVERSATION_STATUS.START)
	const remoteVideoRef = useRef<HTMLVideoElement>(null)

	const peerHandler = () => {
		peer?.on('signal', (data: any) => {
		let payload = { userId: connectionData.userId, signal: data, fromUserId: connectionData.fromUserId }
			socketClient.send({
				type: SocketMessageType.SIGNAL,
				payload
			})
		})

		peer?.on('connect', () => {
			console.log('peer connected: ', localStream)
		})

		peer?.on('error', (err) => {
			console.log('peer error-', err)
			if (peer != null) {
				peer?.destroy()
				peer = null
			}
		})

		peer?.on('track', (track: MediaStreamTrack, stream: MediaStream, transceiver: RTCRtpTransceiver) => {
			setConversationStatus(CONVERSATION_STATUS.IN_CALL)
			console.log('peer track-', track, stream.id, transceiver)
			setRemoteStream(stream)
		})
	}

	const handleEndCall = () => {
		console.log('destroy peer')
		peer?.destroy()
		peer = null
		setRemoteStream(null)
		setConversationStatus(CONVERSATION_STATUS.START)
	}

	useEffect(() => {
		console.log('MakeCall useEffect')
		socketClient = initSocketClient()
		try {
			socketClient.on('open', () => {
				console.log('Socket is openned')
			})
			socketClient.on('message', (data) => {
				switch(data.type) {
					case SocketMessageType.CONFIG: {
						const { ice } = data.payload
						peerOptions.config.iceServers = ice
						console.log('SocketMessageType.CONFIG', ice)
						break
					}
					case SocketMessageType.CALLING: {
						const { roomId, toUserId, fromUserId } = data.payload
						connectionData.roomId = roomId
						connectionData.userId = fromUserId
						connectionData.fromUserId = toUserId
						setConversationStatus(CONVERSATION_STATUS.WAITING)
						break
					}
					case SocketMessageType.RINGING: {
						console.log('Ringing')
						if (!peer || peer == null) {
							initPeer()
						}
						break
					}
					case SocketMessageType.ACCEPT: {
						if (peer == null) {
							initPeer()
						}
						const receivePayload = data.payload as AnswerPayload
						const payload: ReadyPayload = {
							fromUserId: receivePayload.fromUserId,
							toUserId: receivePayload.toUserId,
							roomId: receivePayload.roomId,
							nickname: ''
						}
						socketClient.send({
							type: SocketMessageType.READY,
							payload
						})
						peerHandler()
						localStream?.getTracks().forEach(track => {
							peer?.addTrack(track, localStream)
						})
						break
					}
					case SocketMessageType.SIGNAL: {
						const { signal } = data.payload
						console.log('signal', signal)
						peer?.signal(signal)
						break
					}
					case SocketMessageType.PUBTRACK: {
						const { trackId, type, pubClientId } = data.payload
						if (type == TrackEventType.Add) {
							socketClient.send({
								type: SocketMessageType.SUBTRACK,
								payload: {
									trackId: trackId,
									type: TrackEventType.Sub,
									pubClientId: pubClientId,
								}
							})
						}
						break
					}
					case SocketMessageType.ENDCALL: {
						handleEndCall()
						break
					}
				}
			})
			
		} catch (error) {
			Sentry.captureException(error)
		}
	}, [])

	const makeCall = useCallback(() => {
		try {
			const payload: MakeCallPayload = {
				queueId: '',
				requestType: 'Ekyc',
				customerType: 1,
				metadata: '',
				deviceInfo: 'chrome,linux'
			}
			
			socketClient.send({
				type: SocketMessageType.MAKE_CALL,
				payload
			})
			
		} catch (error) {
			console.log('make call error', error)
			Sentry.captureException(error)
		}
	}, [])

	const toggleCamera = useCallback(() => {
		if (localStream) {
			localStream.getVideoTracks()[0].enabled = !localStream.getVideoTracks()[0].enabled
			setEnableCam(localStream.getVideoTracks()[0].enabled)
		}
	} , [])
	const toggleMic = useCallback(() => {
		if (localStream) {
			localStream.getAudioTracks()[0].enabled = !localStream.getAudioTracks()[0].enabled
			setEnableMic(localStream.getAudioTracks()[0].enabled)
		}
	} , [])

	const endCall = useCallback(() => {
		socketClient.send({
			type: SocketMessageType.ENDCALL,
			payload: {
				fromUserId: connectionData.userId,
				toUserId: connectionData.fromUserId,
				roomId: connectionData.roomId,
			}
		})
		handleEndCall()
	
	}, [])
	
	return (
		<div className="app-Wrapper">
			<div className="app-Wrapper_Stream">
				<div className={conversationStatus !== CONVERSATION_STATUS.START ? 'app-Wrapper_StreamLocal app-Wrapper_StreamLocal-fixed' : 'app-Wrapper_StreamLocal'}>
					<div className="app-Wrapper_StreamLocalWrapper">
						<Camera srcObject={localStream} />
						{
							conversationStatus === CONVERSATION_STATUS.START && (
								<div className="app-Wrapper_StreamLocalWrapperActions">
									<button className="btn-icon mic" onClick={toggleMic}>{ enableMic ? <MdMic /> : <MdMicOff /> }</button>
									<button className="btn-icon cam" onClick={toggleCamera}>{ enableCam ? <MdVideocam /> : <MdVideocamOff />}</button>
									<button className="btn-icon call" onClick={makeCall}><MdCall /></button>
							</div>
							)
						}
					</div>
				</div>
				{
					conversationStatus !== CONVERSATION_STATUS.START &&
						(
							<div className="app-Wrapper_StreamRemote">
									{
										remoteStream && (<Camera srcObject={remoteStream} />)
									}
									{
										conversationStatus === CONVERSATION_STATUS.WAITING && (
											<div className="app-Wrapper_StreamRemoteWaiting"> <span className="app-Wrapper_StreamRemoteWaitingContent"> Ringing </span> </div>
										)
									}

									<div className="app-Wrapper_StreamLocalWrapperActions">
										<button className="btn-icon mic" onClick={toggleMic}>{ enableMic ? <MdMic /> : <MdMicOff /> }</button>
										<button className="btn-icon cam" onClick={toggleCamera}>{ enableCam ? <MdVideocam /> : <MdVideocamOff />}</button>
										<button className="btn-icon end-call" onClick={endCall}><MdCallEnd /></button>
								</div>
							</div>
						)
				}
			</div>
		</div>
	)
}
