import { useEffect } from 'react'

import '../styles/sideWide/LocalStream.scss'

interface Props {
	onLocalStreamReady: (stream: MediaStream) => void
}

function LocalStream({onLocalStreamReady}: Props) {
	
	useEffect(() => {
		console.log('LocalStream: useEffect')
		navigator.mediaDevices.getUserMedia({ video: { width: 480, height: 640 }, audio: true }).then(stream => {
			onLocalStreamReady(stream)
		})
	}, [])

	return null
}

export default LocalStream
